html,
body {
	padding: 0;
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu,
		Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}

a {
	color: inherit;
	text-decoration: none;
}

* {
	box-sizing: border-box;
}

.tldraw {
	position: fixed;
	top: 90px;
	left: 80px;
	width: calc(100% - 80px);
	height: calc(100% - 90px);
}

.p-md {
	padding: 12px;
}

.flex {
	display: flex;
}

.space-between {
	justify-content: space-between;
}

.color-dodgerblue {
	color: dodgerblue;
}

.absolute {
	position: absolute;
	left: 86px;
	top: 8px;
	z-index: 100;
	width: max-content;
	padding: 3px 5px;
	background: #316491;
	border-radius: 5px;
	color: white;
	font-weight: bold;
}
